@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Book.ttf);
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url(./fonts/Avenir-Heavy.ttf);
  font-style: normal;
  font-weight: 600;
}

body {
  font-family: Avenir, sans-serif;
}

:root {
  --color-text: #3d4e61;
}

* {
  box-sizing: border-box;
  padding: 0;
}

.form {
  margin: 20px auto 0;
}

.label {
  position: relative;
  display: block;
  height: 70px;
}

.label__small {
  height: 42px;
}

.input {
  width: 100%;
  height: 100%;
  font-size: 16px;
  padding-left: 62px;
  line-height: 22px;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  color: var(--color-text);
  font-family: inherit;
  font-weight: 300;
  outline: none;
}

.input__small {
  font-size: 14px;
  line-height: 19px;
  padding-left: 33px;
}

.input::placeholder {
  color: inherit;
  font-weight: 300;
}

.image {
  color: #687c94;
  position: absolute;
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.image--small {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.input:hover {
  box-shadow: 0 3px 8px 0 rgba(61, 78, 97, 0.2);
}

.input:focus {
  font-family: inherit;
  font-weight: 600;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
}
